.Card{
    background-color: #171717;
    height: 262px;
    width: 182px;
    overflow: hidden;
    color: #fff;
    border-radius: 10px;
    padding: 16px;
    transition: all .2s;
}
.Card:hover{
    background-color: #232323;
}
.Card_upper{
    margin: 0 0 16px 0;
    display: flex;
    justify-content: center;
    position: relative;
    & img{
        width: 150px;
        height: 150px;
        margin: 0 auto;
        border-radius: 5px;
    }
}
.Card_lower{
    & h5{
        font-size: 15px;
        font-weight: 600;
    }
    & p{
        font-size: 11.2px;
        height: 35px;
        overflow: hidden;
        margin-top: 5px;
        color: #979797;
        font-weight: 500;
    }
}
.play_btn{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #1ed760;
    padding: 14px;
    width: 45px;
    height: 45px;
    position: absolute;
    bottom: 0px;
    right: 0;
    transition: all ease-in-out 0.1s;
}
.play_btn:hover{
    width: 47px;
    height: 47px;
}
.play_btn img{
    width: 21px;
    height: 21px;
}
@media screen and (max-width:434px) {
    .Card{
        background-color: transparent;
        padding: 0;
        width: 155px;
        height: 236px;
    }
}