background-noise {
    background-image: url(data:image/svg+xml;base64,);
}
.Tracks_list{
    /* box-sizing: content-box; */
    /* background-color: beige; */
    /* width: 100%; */
    display: flex;
    flex-direction: column;
    /* padding: 1rem; */
    /* border: 2px solid ; */
    position: relative;
}
.Tracks_list_upper{

    min-height: 18rem;
    /* padding-bottom: 10rem; */
    padding: 5rem 1rem 7rem 1rem;
    display: flex;
    align-items: flex-end;
    flex-grow: 100;
    position: relative;
    top: -65px;
    flex: 1;
    /* border: 2px solid rgb(255, 0, 255); */
}
.Tracks_list_upper::after{
    content: '';
    width: 100%;
    height: 81%;
    top: 0;
    position: absolute;
    right: 0;
    background: linear-gradient(transparent 0,rgba(0,0,0,.5) 100%), url(data:image/svg+xml;base64,);;
}
.Tracks_list_upper h1{
    font-size: 5rem;
    margin: 0.08em 0px 0.12em;
    color: #fff;
    z-index: 999;

}
.Tracks_list_lower{
    /* border: 2px solid rgb(72, 255, 0); */
    /* width: calc(100% - 4rem); */
    position: relative;
    top: -10rem;
    z-index: 999;
    /* padding: 1rem; */
    padding: 1rem;
    /* opacity: 0.8; */
    background: #121212;
    border-radius: 0 0 10px 10px ;
    isolation: isolate;
}
