.Tracks_list_lower_uper{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.Tracks_list_lower_uper_side{
    display: flex;
    align-items: center;
    gap: 25px;
}
.play_btn{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: #1ed760;
    padding: 14px;
}
.play_btn img{
    width: 21px;
}
.like_icone img{
    width: 40px;
}
.list_icone img{
    width: 40px;
    transform: rotate(90deg);
}
.view{
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 5px;
    color: #fff;
}
.view img{
    width: 15px;
}
.tracks_list_breadcrum{
    display: grid;
    color: #fff;
    font-size: 13px;
    text-transform: capitalize;
    align-items: center;
    margin-top: 1rem;
    border-bottom: 1px solid  hsla(0, 0%, 100%, 0.308);;
    padding-bottom: 4px;
    padding-left: 14px;
    gap: 10px;
    margin-bottom: 12px;
    grid-template-columns: [index] 16px [first] 6fr [var1] 4fr [var2] 3fr [last] minmax(120px,1fr);
}
.time {
    display: flex;
    justify-content: center;
    & img{

        width: 21px;
    }
}
.Tracks_list_songs{
    gap: 10px;
    display: grid;
}
.baclBlure{
    /* content: ''; */
    position: absolute;
    width: 100%;
    height: 232px;
    /* background-color: #00000024; */
    /* background-color: rgb(220, 20, 140); */
    z-index: -1;
    top: 0;
    right: 0;
    background-image: linear-gradient(rgba(0,0,0,.6) 0,#121212 100%), url(data:image/svg+xml;base64,);
}
.Tracks_list_upper_left img{
    width: 232px;
    height: 232px;
    box-shadow: 0px 0px 35px 0px rgba(0,0,0,0.75);
    z-index: 998;
}
.Tracks_list_upper_right{
    margin-left: 20px;
    z-index: 998;
    & p{
        font-size: 13px;
        font-weight: 500;
    }
    & h1{
        font-size: 47px;
    }
}
@media screen and (max-width:1000px) {
    .Tracks_list_upper_left img{
        width: 210px;
        height: 210px;
    }
    .Tracks_list_upper_right h1{
        font-size: 40px;
    }
}
@media screen and (max-width:800px) {
    .tracks_list_breadcrum{
        display: none;
    }
    .Tracks_list_songs{
        margin-top: 20px;
    }
    .Tracks_list_upper{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Tracks_list_upper_right h1{
        font-size: 30px;
    }
    .Tracks_list_upper_right{
        margin: 0;
    }
    .play_btn{
        padding: 12px;
    }
    .like_icone img{
        width: 33px;
    }
    .list_icone img{
        width: 32px;
    }
}