.Songs{
    display: grid;
    grid-template-columns: [index] 16px [first] 6fr [var1] 4fr [var2] 3fr [last] minmax(120px,1fr);
    color: #fff;
    gap: 10px;
    height: 50px;
    align-items: center;
    overflow: hidden;
    font-size: 14px;
    padding-left: 14px;
    border-radius: 4px;
}
.Songs:hover{
    background-color: hsla(0, 0%, 100%, 0.17);
}
.songs_title {
    display: flex;
    gap: 10px;
    align-items: center;
    overflow: hidden;
    & img{
        width: 40px;
        height: 40px;
        border-radius: 4px;
    }
}
.name{
    height: 40px;
    overflow-y: clip;
}
.time{
    display: flex;
    justify-content: center;
}
@media screen and (max-width:800px) {
    .Songs{
        grid-template-columns: 1fr 20px;
        padding: 0;
    }
}