.Audio{
    position: sticky;
    bottom: 0;
    /* border: 1px solid #fff ; */
    width: 100%;
    grid-column: 1 / span 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 72px;
    background-color:black;
}
.controls{
    

    max-width: 722px;
    width: 40%;
    /* border: 1px solid red; */

    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.progressBar {
    width: 100%;
    height: 4px;
    background-color: hsla(0, 0%, 100%, 0.3);
    /* background-color: hsla(0, 0%, 100%, 0.3); */
    appearance: none;
}
/* .progressBar:hover{
    background-color: #1db954;
} */
/* .progressBar::-webkit-slider-track {
    width: 4px;
     height: 4px;
    appearance: none;
    color: #1db954;
} */

.progressBar::-webkit-slider-thumb {
    background-color: hsla(0, 0%, 100%, 0.3);
    appearance: none;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #fff;
}
.volume_bar{
    height: 4px;
}

.playIcons {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;
    & img{
        width: 24px;
        height: 24px;
    }
}
.play_border{
    background-color: #fff;
    padding: 7px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    & img{
        width: 22px;
        height: 22px;
    }

}
.volume_controls{
    display: flex;
    align-items: center;
    gap: 10px;
    min-width: 180px;
    /* border: 1px solid #fff; */
    width: 30%;
    justify-content: flex-end;
    padding-right: 10px;
    & img{
        width: 20px;
        height: 20px;
    }
}
.current_audio{
    min-width: 180px;
    padding-inline-start: 8px;
    width: 30%;
    /* border: 1px solid; */
    display: flex;
    gap: 10px;
    align-items: center;
    font-size: 13px;
    font-weight: 500;
    & p{
        /* border: 1px solid red; */
        width: 100%;
        overflow: hidden;
        margin: 0;
        color: #fff;
    }
}
.current_audio_img{
    width: 50px;
    height: 50px;
    border-radius: 5px;
}
.like_icone img{
    width: 25px;
}
@media screen and (max-width:800px) {
    .volume_controls{
        display: none;
    }
    .Audio{
        position: fixed;
        bottom: 73px;
        z-index: 1000;
        /* background-color: #cb1313; */
        margin: 0 7px ;
        width: calc(100% - 14px);
        border-radius: 5px;
        height: 62px;
    }
    .progressBar {
    position: absolute;
    bottom: 0;
    right: 0;
    }
    .playIcons span img:nth-child(1),img:nth-child(3){
        display: none;
    }
    .current_audio{
        width: 85%;
    }
    .play_border{
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translate(0% , -50%);
    }
    .like_icone img{
        width: 36px;
        position: absolute;
        right: 55px;
        top: 50%;
        transform: translate(0% , -50%);
    }
    .current_audio_img{
        width: 45px;
        height: 45px;
    }
    .play_border{
        background-color: transparent;
    }
    .playIcons img{
        width: 26px;
        height: 26px;
    }
}