.NavBar{
    height: 64px;
    width: 100%;
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
    position: sticky;
    top: 0;
    right: 0;
    z-index: 1000;
    transition: all ease-in-out 0.2s;
}
.btn {
    display: flex;
    gap: 20px;
    align-items: center;
    & button{
        background-color: transparent;
        outline: none;
        border: none;
        cursor: pointer;
    }
}
.btn_outer:hover{
    background-color: hsla(0, 0%, 100%, 0.17);
}
.btn_outer{
    padding: 5px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    border-radius: 50%;
}
.rightSide{
    display: flex;
    gap: 10px;
}
.rightSide_install{
    display: flex;
    align-items: center;
    background-color: rgba(0,0,0,0.54);
    padding: 5px 10px;
    border-radius: 10px;
    font-size: 13px;
    color: #fff;
    gap: 5px;
}
.rightSide_install:hover{
    transform: scale(1.1);
}
.rightSide_bell{
    background-color: rgba(0,0,0,0.54);
    padding: 5px ;
    border-radius: 50%;
    width: 38px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.rightSide_bell:hover{
    transform: scale(1.1);
}
.search{
    background-color: #242424;
    border-radius: 40px;
    display: flex;
    align-items: center;
    width: 23vw;
    padding-right: 10px;
}
.search:focus {
    border: 1px solid #fff;
}
.search input{
    padding: 10px 5px 10px 15px;
    width: 20vw;
    font-size: 16px;
    color: #c8c8c8;
    border: none;
    outline: none;
    border-radius: 40px;
    background-color: transparent;
   
}
@media screen and (max-width:900px) {
    .btn {
        gap: 14px;
    }
    .rightSide_install{
        border-radius: 50%;
    }
    .rightSide_install p{
        display: none;
    }
}
@media screen and (max-width:700px) {
.search{
    position: absolute;
    right: 3px;
    width: 73%;
    
}
.search input{
    width: 93%;
}
}