.SideBar{
    height: calc(100vh - 87px);
    max-width: 350px;
    min-width: 350px;
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    margin: 5px 0 0 7px;
    flex-direction: column;
    gap: 10px;
    /* border: 2px solid red; */
    color: #fff;
    font-size: 16px;
    font-weight: 600;
}
.SideBar_upper{
    background-color: #121212;
    border-radius: 10px;
    flex: 2;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 8px 6px;
}
.SideBar_upper li{   
    list-style: none;
}
.Link{
    text-decoration: none;
    color: #fff;
    display: flex;
    gap: 10px;
    padding: 18px 0 10px 12px;
}
.SideBar_lower{
    background-color: #121212;
    border-radius: 10px;
    flex: 10;
    padding: 0 6px;
}
.SideBar_lower li{
    list-style: none;
    display: flex;
    gap: 10px;
    padding: 18px 0 10px 12px;
}
.playlist{
    background-color: #242424d3;;
    padding: 18px 0 10px 12px;
    border-radius: 8px;
    margin-top: 20px
    
}
.playlist h5{
    font-size: 15px;
    font-weight: 500;
}
.playlist p{
    font-size: 13px;
    font-weight: 400;
    margin: 7px 0;
}
.playlist button{
    padding: 9px 15px;
    border: navajowhite;
    outline: none;
    font-weight: 700;
    border-radius: 50px;
    font-size: 14px;
    margin: 11px 0;
}
@media screen and (max-width:1000px) {
    .SideBar{
        min-width: 300px;
    }
}
@media screen and (max-width:800px) {
    .SideBar{
        min-width: 100%;
        gap: 0;
        height: 70px;
        flex-direction: row;
        background: linear-gradient(0deg,#000 40%,rgba(0,0,0,.1));
        position: fixed;
        bottom: 0;
        top: unset;
        z-index: 1000;
        font-size: 13px;
    margin: 0;
    }
    .playlist{
        display: none;
    }
    .SideBar_upper{
        flex-direction: row;
        min-width: 44%;
        justify-content: space-around;
        align-items: center;
        padding: 0;
        background-color: transparent;
    }
    .Link{
        flex-direction: column;
        align-items: center;
        padding: 0;
        gap: 7px;
    }
    .SideBar_lower{
        min-width: 44%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 0;
        background-color: transparent;
    }
    .SideBar_lower li{
        flex-direction: column;
        align-items: center;
        padding: 0;
    }
    .SideBar_lower li{
        gap: 7px;
    }
    .spotifyApp{
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-items: center;
        cursor: pointer;
        & img{
            width: 23px;
            filter: grayscale(100%);
        }
    }

}