.Category{
    text-decoration: none;
}
.Category_container{
    /* border: 1px solid; */
    width: 11.5rem;
    height: 12rem;
    border-radius: 12px;
    position: relative;
    overflow: hidden;
    background-color: blueviolet;
}
.Category_container p{
    text-decoration: none;
    color: #fff;
    font-size: 22px;
    font-weight: 500;
    margin: 10px;
}
.Category_container img{
    transform: rotate(25deg) translate(18%,-2%);
    width: 100px;
    height: 100px;
    position: absolute;
    bottom: 0;
    right: 0;

}
@media screen and (max-width:600px) {
    .Category_container{
        width: 44vw;
        height: 6.2rem;
    }
    .Category_container img{
        width: 65px;
        height: 65px;
    }
    .Category_container p{
        font-size: 18px;
    }
}