.Search_constainer{
    /* padding: 0 0 10px 1rem ; */
    background-color: #121212;
    margin: 5px 0 0 0;
    border-radius: 10px;
    height: calc(100vh - 87px);
    overflow-y: scroll;
}
.Search_constainer h4{
    font-size: 24px;
    color: #fff;
    padding: 0 0 10px 1rem ;
}
.Search_category{
    display: flex;
    padding: 0 0 10px 1rem ;
    flex-wrap: wrap;
    width: 100%;
    gap: 10px;
}
@media screen and (max-width:800px) {
    .Search_constainer{
        height:100vh;
    }
    
}
@media screen and (max-width:600px) {
    .Search_category{
        gap: 14px;
    }
}
@media screen and (max-width:434px) {
    .Search_category{
        justify-content: center;
    }
}