.Home{
    background-color: #121212;
    margin: 5px 0 0 0;
    border-radius: 10px;
    height: calc(100vh - 87px);
    overflow-y: scroll;
    color: #fff;
    
}
.Home_rows{
    display: flex;
    flex-direction: column;
    gap: 20px;
    /* border: 2px solid red; */
    padding: 1rem;
}
.Home_Cards{
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}
@media screen and (max-width:800px) {
    .Home{
        height:100vh;
    }
    .moring{
        padding: 1rem;
        display: flex;
        justify-content: space-between;
        & h3{
            font-size: 24px;
        }
        & img{
            width: 30px;
            height: 30px;
        }
    }
}
@media screen and (max-width:443px) {
    .Home_Cards{
        gap: 17px;
        justify-content: center;
    }
}