.Footer_upper{
    display: grid;
    grid-template-columns: 1fr 200px;
    width: 100%;
    padding: 2rem 1rem;
    margin-top: 25px;
}
.Footer_left{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}
.Footer_left_text{
    width: 50%;
    max-width: 200px;
}
.Footer_left_text h4{
    font-size: 15px;
    font-weight: 500;
    padding: 0 !important;
}
.Footer_left_text p{
    font-size: 13px;
    font-weight: 400;
    color: #989898;
    margin: 8px 0;
}
.Footer_right{
    display: flex;
    gap: 20px;
}
.Footer_right_icone{
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #393939;
    height: 45px;
    width: 45px;
    border-radius: 50%;
    
}
.Footer_lower{
    display: grid;
    grid-template-columns: 1fr 200px;
    width: 100%;
    padding: 1rem 1rem 4rem 1rem;
    padding-top: 40px;
    border-top: 1px solid #515151;
}
.Footer_right_icone:hover{
    background-color: #515151;
}
.Footer_lower_textt{
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
}
.Footer_lower_textt li{
    font-size: 13px;
    color: #989898;
    list-style: none;
}
@media screen and (max-width: 600px) {
    .Footer_upper{
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    .Footer{
        padding-bottom: 50px;
    }
}